@media  screen and (max-width: 800px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

    /* Force table to not be like tables anymore */
    .assingmentTable,.assingmentTable thead,.assingmentTable tbody,.assingmentTable th,.assingmentTable td,
    .assingmentTable{
        border-collapse: separate;
    }
    .assingmentTable tr {
        display: block;
    }

    .assingmentTable  td:first-child{background-color: inherit;
        border: 1px solid;
        border-top: 1px  transparent;
        border-right: 0px solid #555;
        border-bottom: 0px solid transparent;
        width: 50px;
        height: 0;


    }

    /* Hide table headers (but not display: none;, for accessibility) */
  .assingmentTable  thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

   .assingmentTable tr { border: 1px solid #FFFFFF; }


   .assingmentTable  td {
        /* Behave  like a "row" */
        border: none;
        border-bottom: 1px solid #eee;
        position: relative;
        padding-left: 50%;
    }

  .assingmentTable  td:before {
        /* Now like a table header */
        position: absolute;
        /* Top/left values mimic padding */
        top: 12px;
        left:11px;
        width: 25%;
        padding-right: 10px;
        white-space: nowrap;
    }

    /*
    Label the data
    */
   .assingmentTable td:nth-of-type(1):before { content: "Time";

   }
   .assingmentTable td:nth-of-type(2):before { content: "Sunday"; }
   .assingmentTable td:nth-of-type(3):before { content: "Monday"; }
    .assingmentTable td:nth-of-type(4):before { content: "Tuesday"; }
    .assingmentTable td:nth-of-type(5):before { content: "Wednesday"; }
    .assingmentTable td:nth-of-type(6):before { content: "Thrusday"; }
    .assingmentTable td:nth-of-type(7):before { content: "Friday"; }
    .assingmentTable td:nth-of-type(8):before { content: "Saturday"; }

}




@media screen and (max-width: 600px) {
    .assingmentTable {
        border: 0;
    }

    .assingmentTable caption {
        font-size: 1.3em;
    }

    .assingmentTable thead {
       clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }

    .assingmentTable tr {
        border-bottom: 3px solid #ddd;
        display: block;
        margin-bottom: .625em;
    }

    .assingmentTable td {
        border-bottom: 1px solid #ddd;
        display: block;
        font-size: .8em;
        text-align: right;
    }


        /*
        * aria-label has no advantage, it won't be read inside a table
        content: attr(aria-label);
        */
    .assingmentTable td:nth-of-type(2):before { content: "Sunday"; }
    .assingmentTable td:nth-of-type(3):before { content: "Monday"; }
    .assingmentTable td:nth-of-type(4):before { content: "Tuesday"; }
    .assingmentTable td:nth-of-type(5):before { content: "Wednesday"; }
    .assingmentTable td:nth-of-type(6):before { content: "Thrusday"; }
    .assingmentTable td:nth-of-type(7):before { content: "Friday"; }
    .assingmentTable td:nth-of-type(8):before { content: "Saturday"; }

    .assingmentTable td::before {
        float: left;
        font-weight: bold;
        text-transform: uppercase;


    }

    .assingmentTable td:last-child {
        border-bottom: 0;
    }
}






