.timeline{
    width:800px;
    background-color:#FFFFFF;
    color:#fff;
    padding:30px 20px;
    margin-left: 15%;
    box-shadow:0px 0px 10px rgba(0,0,0,.5);
}
.timeline ul{
    list-style-type:none;
    border-left:2px solid #094a68;
    padding:10px 5px;
    background-color: aliceblue;
}
.timeline ul li{
    padding:20px 20px;
    position:relative;
    cursor:pointer;
    transition:.5s;
}
.timeline ul li span{
    display:inline-block;
    background-color:#87CEEB;
    border-radius:15px;
    padding:4px 58px;
    font-size:15px;
    text-align:center;
}
.timeline ul li span:hover{
    display:inline-block;
    background-color:white;
    box-shadow:0px 0px 10px rgba(0,0,0,.5);
    border-radius:15px;
    padding:4px 58px;
    font-size:15px;
    text-align:center;
}
.timeline ul li .co h3{
    color:#000000;
    font-size:17px;
    padding-top:5px;
}
.timeline ul li .co p{
    padding:5px 0px 15px 0px;
    font-size:15px;
    color:#000000;
}
.timeline ul li:before{
    position:absolute;
    content:'';
    width:10px;
    height:10px;
    background-color:black;
    border-radius:50%;
    left:-11px;
    top:28px;
    transition:.5s;
}
.timeline ul li:hover{
    background-color:#d8dff9;
}
.timeline ul li:hover:before{
    background-color:#0F0;
    box-shadow:0px 0px 10px 2px #0F0;
}
@media screen and (max-width: 800px){
    .timeline{
        margin-left: 1%;
        width:100%;
        padding:30px 5px 30px 10px;
    }
    .timeline ul li .co h3{
        color:#34ace0;
        font-size:15px;
    }

}

