.page-header nav {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

.page-header .logo {
    display: block;
    max-width: 100px;
    margin: 0 25px;
}

.page-header .toggle-mob-menu {
    display: none;
    margin-left: 5px;
    padding: 4px;
    background: #fff;
    background: var(--page-content-blockColor);
    border-radius: 4px;
    border-radius: var(--border-radius);
}

.page-header .toggle-mob-menu svg {
    fill: #333;
    fill: var(--black);
    transition: transform 0.2s;
}

.page-header .admin-menu {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-top: 15px;
}

/*.page-header .admin-menu li:nth-last-child(2) {
  margin-bottom: 35px;
}

.page-header .admin-menu li:last-child {
  margin-top: auto;
  margin-bottom: 20px;
}
*/
.page-header .admin-menu li > * {
    width: 100%;
    padding: 10px 15px;
}

.page-header .admin-menu .menu-heading h3 {
    text-transform: uppercase;
    letter-spacing: 0.15em;
    font-size: 12px;
    margin-top: 12px;
    color: #7889a4;
    color: var(--page-header-headingColor);
}

.page-header .admin-menu svg {
    width: 40px;
    height: 35px;
    fill: #dde9f8;
    fill: var(--page-header-txtColor);
    margin-right: 10px;
    transition: fill 0.2s;
}

.page-header .admin-menu a,
.page-header .admin-menu button {
    display: flex;

    font-size: 15px;
    font-weight: 200;
    transition: background 0.2s, color 0.2s;
}

.page-header .admin-menu a:hover,
.page-header .admin-menu a:focus,
.page-header .admin-menu button:hover,
.page-header .admin-menu button:focus {
    background: #1d2636;
    background: var(--page-header-bgColor-hover);
    color: #00b9eb;
    color: var(--blue);
    outline: none;
}

.page-header .admin-menu a:hover svg,
.page-header .admin-menu a:focus svg,
.page-header .admin-menu button:hover svg,
.page-header .admin-menu button:focus svg {
    fill: #00b9eb;
    fill: var(--blue);
}
.dropdown-btn {

    text-decoration: none;
    font-size: 20px;
    color: white;
    display: block;
    border: none;
    background: none;

    cursor: pointer;
    outline: none;
}
.dropdown-container {
    display: none;
    background-color: grey;
    padding-left: 10px;
}

/* Optional: Style the caret down icon */
.fa-caret-down {
    float: right;
    padding-right: 8px;
}


.navbar {
    margin-left: 16.2%;
    overflow: hidden;
    background-color: #d8dff9;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    z-index: 1;
    box-shadow: 0 1px 2px 0;
}

/* Style the navigation bar links */
.navbar a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 20px;
    text-decoration: none;

}


/* Right-aligned link */
.navbar a.right {
    float: right;
    height: 60px;
}

/* Change color on hover */
.navbar a:hover {
    background-color: #ddd;
    color: black;
}

/* Active/current link */
.navbar a.active {
    background-color: #666;
    color: white;

}
.avatar {
    vertical-align: middle;
    width: 40px;
    height: 37px;
    border-radius: 50%;
}

/* PAGE CONTENT STYLES
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.page-content {
    position: relative;
    padding: 30px;
    color: #171616;
    color: var(--page-content-txtColor);
    background: #f0f1f6;
    background: var(--page-content-bgColor);
}

.search-and-user {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-column-gap: 50px;
    align-items: center;
    background: #f0f1f6;
    background: var(--page-content-bgColor);
    margin-bottom: 30px;
}

.search-and-user form {
    position: relative;
}

.search-and-user input[type="search"] {
    width: 100%;
    height: 50px;
    font-size: 1.5rem;
    padding-left: 15px;
    background: #fff;
    background: var(--page-content-blockColor);
    color: #fff;
    color: var(--white);
    border-radius: 4px;
    border-radius: var(--border-radius);
    box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.075);
    box-shadow: var(--box-shadow);
}

.search-and-user ::-webkit-input-placeholder {
    color: #171616;
    color: var(--page-content-txtColor);
}

.search-and-user :-ms-input-placeholder {
    color: #171616;
    color: var(--page-content-txtColor);
}

.search-and-user ::-ms-input-placeholder {
    color: #171616;
    color: var(--page-content-txtColor);
}

.search-and-user ::placeholder {
    color: #171616;
    color: var(--page-content-txtColor);
}

.search-and-user form svg {
    width: 26px;
    height: 26px;
    fill: #171616;
    fill: var(--page-content-txtColor);
}

.search-and-user form button {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
}



.page-content .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
}

.page-content .grid > article {
    display: flex;
    height: 100px;
    background: #fff;
    background: var(--page-content-blockColor);
    border-radius: 4px;
    border-radius: var(--border-radius);
    box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.075);
    box-shadow: var(--box-shadow);
}

.page-content .grid > article:first-child button {

    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 10%;
    font-size: 18px;
}


.page-content .grid > article:first-child{
    display: flex;
    height: 250px;
    width: 100%;
    background: #fff;
    background: var(--page-content-blockColor);
    border-radius: 4px;
    border-radius: var(--border-radius);
    box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.075);
    box-shadow: var(--box-shadow);
}

.page-content .grid > article:last-child {
    grid-column: 1 / -1;


}
.CardInfo {
    box-shadow: 0 4px 8px 0;
    background-color: #FFFFFF;
    max-width: 80%;
    padding: 10px;
    border-left:.25rem solid blue !important;
    text-align: center;
    font-family: "sofia";
    font-style: normal;
    font-feature-settings: normal;
    font-variant: normal;
    font-size: 16px;
}



.CardInfo img {
    vertical-align: middle;
    width: 55px;
    height: 110px;
    border-radius: 8px;

}


/* MQ RULES
–––––––––––––––––––––––––––––––––––––––––––––––––– */

@media screen and (max-width: 800px) {
   .page-content .grid{

        grid-template-columns: repeat(1, 1fr);

    }
}


/* BODY CLASSES
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.mob-menu-opened .toggle-mob-menu svg {
    transform: rotate(180deg);
}

.mob-menu-opened .page-header .admin-menu {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
}

@media screen and (min-width: 768px) {
    .collapsed .page-header .navbar  {
        width: 40px;
    }

    .collapsed .page-header .admin-menu li > * {
        padding: 10px;
    }

    .collapsed .page-header .logo,
    .collapsed .page-header .admin-menu span,
    .collapsed .page-header .admin-menu .menu-heading {
        display: none;
    }

    .collapsed .page-header .admin-menu svg {
        margin-right: 0;
    }

    .collapsed .page-header .collapse-btn svg {
        transform: rotate(180deg);
    }

    .collapsed .page-content {
        left: 40px;
        width: calc(100% - 40px);
    }
}


