.noticebord {
    border-left: 8px solid #4D6875;
    border-bottom-right-radius: 2px;
    border-top-right-radius: 2px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgb(0, 0, 0);
    color: #333;
    width: 100%;
    letter-spacing: 0.5px;
    position: relative;
    line-height: 1.4em;
    padding: 20px;
    list-style: none;
    text-align: left;
    background-color: #FFFFFF;
}

.noticebord h1,
.noticebord h2,
.noticebord h3 {
    font-size: 1.2em;
}

.noticebord .event {
    border-bottom: 1px solid rgba(160, 160, 160, 0.2);
    padding-bottom: 15px;
    margin-bottom: 20px;
    position: relative;
}

.noticebord .event:last-of-type {
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
}

.noticebord .event:before,
.noticebord .event:after {
    position: absolute;
    display: block;
    top: 0;
}

.noticebord .event:before {
    left: -177.5px;
    content: attr(data-date);
    text-align: right;
    color: #00b9eb;
    font-family: cursive;
    /*  font-weight: 100;*/

    font-size: 16px;
    min-width: 120px;
}

.noticebord .event:after {
    box-shadow: 0 0 0 8px #00c853;
    left: -30px;
    background: #212121;
    border-radius: 50%;
    height: 11px;
    width: 11px;
    content: "";
    top: 5px;
}
.noticebord .event:hover {
    background-color: grey;


}

@media (max-width: 945px) {
    .noticebord .event::before {
        left: 0.5px;
        margin-top: 24%;
        top: 20px;
        min-width: 0;
        content: attr(data-date);
        text-align: left;
        font-size: 13px;
    }
    .noticebord h3 {
        font-size: 16px;
    }
    .noticebord p {
        padding-top: 20px;
    }
    section.lab h3.card-title {
        padding: 5px;
        font-size: 16px
    }
}

@media screen and (max-width: 800px) {
    .noticebord .event::before {
        left: 0.5px;
        margin-top: 5%;
        top: 20px;
        min-width: 0;
        content: attr(data-date);
        text-align: left;
        font-size: 13px;
    }
    .noticebord .event:nth-child(1)::before,
    .noticebord .event:nth-child(3)::before,
    .noticebord .event:nth-child(5)::before {
        top: 38px;
    }
    .noticebord h3 {
        font-size: 16px;
    }
    .noticebord p {
        padding-top: 20px;
    }
    .noticebord {
        width: 60%;
    }
}




