/* Time */

.time {
    border-left: 8px solid #4D6875;
    border-bottom-right-radius: 2px;
    border-top-right-radius: 2px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgb(0, 0, 0);
    color: #333;
  width: 50%;
    margin-left: 20%;
    letter-spacing: 0.5px;
    position: relative;
    line-height: 1.4em;
    padding: 20px;
    list-style: none;
    text-align: left;
}

.time h1,
.time h2,
.time h3 {
    font-size: 1.2em;
}

.time .event {
    border-bottom: 1px solid rgba(160, 160, 160, 0.2);
    padding-bottom: 15px;
    margin-bottom: 20px;
    position: relative;
}

.time .event:last-of-type {
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
}

.time .event:before,
.time .event:after {
    position: absolute;
    display: block;
    top: 0;
}

.time .event:before {
    left: -177.5px;
    content: attr(data-date);
    text-align: right;
    color: #00b9eb;
    font-family: cursive;
    /*  font-weight: 100;*/

    font-size: 16px;
    min-width: 120px;
}

.time .event:after {
    box-shadow: 0 0 0 8px #00c853;
    left: -30px;
    background: #212121;
    border-radius: 50%;
    height: 11px;
    width: 11px;
    content: "";
    top: 5px;
}
.time .event:hover {
   background-color: grey;


}

/* selected link */

