@media (max-width: 945px) {
    .time .event::before {
        left: 0.5px;
        margin-top: 24%;
        top: 20px;
        min-width: 0;
        content: attr(data-date);
        text-align: left;
        font-size: 13px;
    }
    .time h3 {
        font-size: 16px;
