        left: 0.5px;
        margin-top: 5%;
        top: 20px;
        min-width: 0;
        content: attr(data-date);
        text-align: left;
        font-size: 13px;
    }
    .time .event:nth-child(1)::before,
    .time .event:nth-child(3)::before,
    .time .event:nth-child(5)::before {
        top: 38px;
    }
    .time h3 {
        font-size: 16px;
    }
    .time p {
        padding-top: 20px;
    }
    .time {
        width: 60%;
    }
