    float: left;
    margin-right: 4px;
    overflow: hidden;
    position: relative;
}
.fileinput-button input {
    cursor: pointer;
    direction: ltr;
    font-size: 23px;
    margin: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(-300px, 0px) scale(4);
}
.fileupload-buttonbar .btn, .fileupload-buttonbar .toggle {
    margin-bottom: 5px;
}
.files .progress {
    width: 200px;
}
.fileupload-processing .fileupload-loading {
    display: block;
}
* html .fileinput-button {
    line-height: 24px;
    margin: 1px -3px 0 0;
}
* + html .fileinput-button {
    margin: 1px 0 0;
    padding: 2px 15px;
}
@media (max-width: 767px) {

    .files .preview * {
        width: 40px;
    }
    .files .name * {
        display: inline-block;
        width: 80px;
        word-wrap: break-word;
    }
    .files .progress {
        width: 20px;
    }
    .files .delete {
        width: 60px;
    }
}
ul {
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}


.submenu {
    margin-left: 16.2%;
    overflow: hidden;
    background-color: #d8dff9;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    z-index: 1;
    box-shadow: 0 1px 2px 0;
}

/* Style the navigation bar links */
.submenu a {
    float: left;
    display: block;
    color: black;
    text-align: center;

    padding: 14px 20px;
    text-decoration: none;
}
.submenu  img {
    float: left;
    display: block;
    color: white;
    margin-top: -20%;


}


/* Right-aligned link */
.submenu a.right {
    float: right;
    height: 60px;
}


/* Change color on hover */
.submenu  a:hover {
    background-color: #ddd;
    color: black;
}

/* Active/current link */
.submenu a.active {
    background-color: #666;
    color: white;

}


